import { mapState, mapMutations } from 'vuex';
import uniTag from '@/components/uni-tag/uni-tag.vue';
import uniDrawer from '@/components/uni-drawer/uni-drawer.vue';
import uniPopup from '@/components/uni-popup/uni-popup.vue';
import uniTransition from '@/components/uni-transition/uni-transition.vue';

var util = require('../../common/util.js');
var colors = require('../../common/colors.js');

export default {
	components: {
		uniTag,
		uniDrawer,
		uniPopup,
		uniTransition
	},
	data() {
		return {
			e: [],
			drawerMode: 0, //0:drawer close; 1: createClass drawer; 2: updateClass drawer;
			currentClass: {
				weekday: 'monday',
				name: '语文',
				time: 480,
				dur: 45,
				durMax: 240,
				icon: 'success',
				margintop: 0,
				height: 0,
				color: 'red'
			},
			// show: {
			// 	middle: false,
			// 	top: false,
			// 	bottom: false,
			// 	right: false,
			// 	right2: false
			// },
			// e: [],
			x: 0,
			y: 0,
			systemInfo: [],
			util1: util,
			timestamp: 'abc',
			colors: colors,
			today: 'aaa',
			bottomData: [
				{
					text: '微信',
					icon: 'https://img-cdn-qiniu.dcloud.net.cn/uni-ui/grid-2.png',
					name: 'wx'
				},
				{
					text: '支付宝',
					icon: 'https://img-cdn-qiniu.dcloud.net.cn/uni-ui/grid-8.png',
					name: 'wx'
				},
				{
					text: 'QQ',
					icon: 'https://img-cdn-qiniu.dcloud.net.cn/uni-ui/gird-3.png',
					name: 'qq'
				},
				{
					text: '新浪',
					icon: 'https://img-cdn-qiniu.dcloud.net.cn/uni-ui/grid-1.png',
					name: 'sina'
				},
				{
					text: '百度',
					icon: 'https://img-cdn-qiniu.dcloud.net.cn/uni-ui/grid-7.png',
					name: 'copy'
				},
				{
					text: '其他',
					icon: 'https://img-cdn-qiniu.dcloud.net.cn/uni-ui/grid-5.png',
					name: 'more'
				}
			],
			showPop: false,
			transfromClass: {
				position: 'fixed',
				bottom: 0,
				top: 0,
				left: 0,
				right: 0,
				width: '100%',
				height: '70%',
				margin: 'auto',
				display: 'flex',
				'justify-content': 'center',
				'align-items': 'center'
			},
			colorList: [
				'#F44336',
				'#e91e63',
				'#9c27b0',
				'#673ab7',
				'#3f51b5',
				'#2196F3',
				'#03a9f4',
				'#00bcd4',
				'#009688',
				'#4CAF50',
				'#8bc34a',
				'#cddc39',
				'#ffeb3b',
				'#ffc107',
				'#ff9800',
				'#ff5722',
				'#795548',
				'#607d8b',
				'#9e9e9e'
			],
			blockToken: 0
		};
	},
	methods: {
		...mapMutations(['increaseCount', 'increaseCountBy', 'decreaseCount', 'updateProjs', 'addClass', 'updateClass', 'deleteClass']),
		// onUpdateClassName: function(e) {
		// 	this.classname = e.detail.value;
		// },
		// onChangeWeekday: function(day) {
		// 	this.weekday = day;
		// },
		// bindTimeChange: function(e) {
		// 	this.cuclassStartTime = e.target.value;
		// },
		getStorage: function() {
			var key = this.key;
			//     data = this.projs;
			if (key.length === 0) {
				uni.showModal({
					title: '读取数据失败',
					content: 'key 不能为空',
					showCancel: false
				});
			} else {
				uni.getStorage({
					key: key,
					success: res => {
						// console.log(JSON.stringify(res.data));
						// uni.showModal({
						//     title: '读取数据成功',
						//     content: "data: '" + res.data + "'",
						//     showCancel: false
						// })
						this.projs = res.data;
					},
					fail: () => {
						uni.showModal({
							title: '读取数据失败',
							content: '找不到 key 对应的数据',
							showCancel: false
						});
					}
				});
			}
		},
		setStorage: function() {
			var key = this.key;
			var data = this.projs;
			if (key.length === 0) {
				uni.showModal({
					title: '保存数据失败',
					content: 'key 不能为空',
					showCancel: false
				});
			} else {
				uni.setStorage({
					key: key,
					data: data,
					success: res => {
						uni.showModal({
							title: '存储数据成功',
							content: ' ',
							showCancel: false
						});
					},
					fail: () => {
						uni.showModal({
							title: '储存数据失败!',
							showCancel: false
						});
					}
				});
			}
		},
		clearStorage: function() {
			uni.clearStorageSync();
			(this.key = ''), (this.data = '');
			uni.showModal({
				title: '清除数据成功',
				content: ' ',
				showCancel: false
			});
		},
		dirto: function() {
			console.log('dirto');
			uni.navigateTo({
				url: '/pages/viewtest/viewtest'
			});
		},
		formatDate: function(date) {
			var h = parseInt(date / 60);
			var s = date % 60;
			s = s + '';
			// console.log('second:' + s + ', second length:' + s.length)
			if (s.length == 1) s = '0' + s;
			return [h, s].join(':');
		},
		onNewClass(e) {
			let weekday = 'monday';
			switch (e.currentTarget.dataset.x) {
				case 0:
					weekday = 'monday';
					break;
				case 1:
					weekday = 'tuesday';
					break;
				case 2:
					weekday = 'wednesday';
					break;
				case 3:
					weekday = 'thursday';
					break;
				case 4:
					weekday = 'friday';
					break;
				case 5:
					weekday = 'saturday';
					break;
				case 6:
					weekday = 'sunday';
					break;
				default:
					weekday = 'monday';
					break;
			}
			this.currentClass = {
				weekday: weekday,
				name: '语文',
				time: 480,
				dur: 45,
				icon: 'success',
				margintop: 0,
				height: 0
			};
			this.drawerMode = 2;
			this.x = e.currentTarget.dataset.x;
		},
		onEditClass(e) {
			console.log('onEditClass');
			// console.log(JSON.stringify(e.currentTarget.dataset));
			this.drawerMode = 1;
			this.e = e;
			this.currentClass = e.currentTarget.dataset.class;
			this.x = e.currentTarget.dataset.x;
			this.y = e.currentTarget.dataset.y;
		},
		onPopup(e) {
			// this.$refs.popup.open();
			this.currentClass = e.currentTarget.dataset.class;
			this.x = e.currentTarget.dataset.x;
			this.y = e.currentTarget.dataset.y;
			this.showPop = !this.showPop;
		},
		toggle(type) {
			// console.log(type);
			this.show[type] = !this.show[type];
		},
		submitUpdateClass: function() {
			console.log('submitUpdateClas');
			// console.log(JSON.stringify(e.currentTarget.dataset))
			this.currentClass.height = this.currentClass.dur * this.projs.rpx;
			this.updateClass({ currentClass: this.currentClass, x: this.x, y: this.y });
			this.updateProjs();
			this.drawerMode = 0;
			this.showPop = false;
			// this.cancelCreateProj();
		},
		submitAddClass: function() {
			console.log('submitCreateProj');
			this.currentClass.height = this.currentClass.dur * this.projs.rpx;
			this.addClass(this.currentClass);
			this.updateProjs();
			this.drawerMode = 0;
		},
		cancelCreateProj: function() {
			this.drawerMode = 0;
			this.showPop = false;
		},
		onDeleteCurrClass: function() {
			console.log('onDeleteCurrClass');
			// console.log(JSON.stringify(this.currentClass));
			this.deleteClass({ currentClass: this.currentClass, x: this.x, y: this.y });
			this.updateProjs();
			this.drawerMode = 0;
			this.showPop = false;
		},
		sliderClassTimeChange: function(e) {
			this.currentClass.time = e.detail.value;
		},
		sliderClassDurChange: function(e) {
			this.currentClass.dur = e.detail.value;
		},
		onSubClassTime: function() {
			this.currentClass.time--;
		},
		onAddClassTime: function() {
			this.currentClass.time++;
		},
		onSubClassDur: function() {
			this.currentClass.dur--;
		},
		onAddClassDur: function() {
			this.currentClass.dur++;
		},
		onExtendClassDurMax: function() {
			this.currentClass.durMax += 10;
			// this.currentClass.dur ++;
		},
		onChangeWeekday: function(day) {
			this.currentClass.weekday = day;
		},
		onUpdateClassName: function(e) {
			this.currentClass.name = e.detail.value;
		},
		onUpdateStartTime: function(e) {
			this.currentClass.time = e.detail.value;
		},
		isEmphasis: function(i) {
			// console.log('isEmphasis');
			let weex = new Date().getDay();
			// console.log(weex);
			if (i == weex - 1) return true;
			else return false;
		},
		transChange: function() {},
		onSelectColor: function(e) {
			this.e = e;
			// console.log(e.currentTarget.dataset.color);
			this.blockToken = e.currentTarget.dataset.index;
		}
	},
	computed: {
		projs: {
			get: function() {
				console.log('projs get');
				return this.$store.state.projs;
			},
			set: function(val) {
				console.log('projs set');
				this.$store.commit('setProjs', val);
			}
		}
	},
	onLoad() {
		// 获取系统信息
		console.log('getSystemInfo');
		uni.getSystemInfo({
			success: res => {
				this.systemInfo = res;
				this.$store.commit('setTableHeight', res.windowHeight);
			}
		});
		//this.loadData();
		//this.getStorage();
		this.updateProjs();
		this.today = this.isEmphasis(1);
	},
	created() {
		// this.timestamp = new Date().getTime();
		// console.log('timestamp:' + this.timestamp);
	}
};
